.navbar {
  background-color: white; 
  padding: 10px 20px; 
  display: flex;
  justify-content: flex-end; 
  align-items: center;
  position: sticky; 
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); 
}

.nav-menu {
  list-style: none; 
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-menu li {
  margin: 0 40px; 
  position: relative;
}

.nav-menu a {
  color: black; 
  text-decoration: none; 
  font-size: 20px; 
  font-weight: 600; 
  font-family: 'Inter','Arial', sans-serif; 
  transition: color 0.3s ease, transform 0.3s ease;
}

.nav-menu a:hover {
  color: #0077b6;
  transform: scale(1.05); 
}

.nav-menu li hr {
  margin: 0;
  border: none;
  height: 2px;
  width: 100%;
  background-color: #0077b6; 
  position: absolute;
  bottom: -5px; 
  left: 0;
  animation: grow 0.3s ease-in-out;
}



